import React from "react";
import { Container } from "../../style/Container_style";
import IMG from "../../../Images/defaultImg.jpg";
import { Link } from "react-router-dom";
import { PoseResultForm } from "./style/PoseResultCom_style";

const PoseResultCom = () => {
  return (
    <>
      <Container>
        <PoseResultForm>
          <div className="topButton">
            <Link to="/Pose"></Link>
            <Link to="/Pose/Squat/"></Link>
          </div>
          <div className="leftForm">
            <div className="titleFrom">
              <img className="profileIMG" src={IMG}></img>
              <dl className="infoForm">
                <dt></dt>
                <dd>&#9734; : 4.5</dd>
                <dd> : 4.5</dd>
              </dl>
            </div>
            <dl>
              <dt className="descriptionForm"> </dt>
              <dd className="explainForm"> </dd>
            </dl>
          </div>
        </PoseResultForm>
      </Container>
    </>
  );
};

export default PoseResultCom;
