import React, { useState } from "react";
import { Fullscreen, ModalBlock, StyledButton } from "./style/PoseModal_style";

const PoseModal = ({
  title,
  onCancel,
  visible,
  onIncrease,
  onDecrease,
  poseCount,
  onStartButton,
}) => {
  return (
    <Fullscreen visible={visible}>
      <ModalBlock>
        <h2>{title}</h2>
        <div className="flex">
          <div className="times">
            <h3 className="context"></h3>
            <button type="button" onClick={() => onDecrease(poseCount)}>
              &lt;
            </button>
            <h3>{poseCount}</h3>
            <button type="button" onClick={() => onIncrease(poseCount)}>
              &gt;
            </button>
          </div>
        </div>
        <h2> </h2>
        <div className="flex">
          <button className="recentTime" type="submit">
            1
          </button>
          <button className="recentTime" type="submit">
            2
          </button>
          <button className="recentTime" type="submit">
            3
          </button>
        </div>
        <div className="buttons">
          <StyledButton type="button" onClick={onCancel}>
            
          </StyledButton>
          <StyledButton
            type="button"
            cyan
            onClick={() => poseCount > 0 && onStartButton()}
          >
            
          </StyledButton>
        </div>
      </ModalBlock>
    </Fullscreen>
  );
};
export default PoseModal;
